*, *:before, *:after {
	box-sizing: border-box;
}

html {
    font-size: 16px;
}

body {
    font-family: $font;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.5;
    color: $text_c;
    min-width: 1345px;
    overflow-x: hidden;

    @media(max-width: 1345px) {
        overflow-x: auto;
    }
}